
.side-menu {
  display: flex;
  align-items: center;

  border-right: 1px solid #b4bef0;

  @media(min-width: 1024px) and (max-width: 1439px) {
    display: grid;
    align-items: center;

    grid-column-end: -1;
    grid-column-start: 1;
    grid-row: 1 / 2;
    grid-template-columns: 200px 1fr 32px;
  }

  @media(min-width: 1440px) {
    display: grid;
    align-items: flex-start;

    grid-column-end: 2;
    grid-column-start: 1;
    grid-row: 1 / 3;
    grid-template-rows: 80px 1fr auto;
  }
}

.side-menu__logo {
  margin: auto;
  margin-left: 30px;

  a {
    display: block;
  }
}

.mobile-menu {
  display: none;
  @media(max-width: 1023px) {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 46px;
    height: 46px;
    margin-right: 20px;

    background: #f1f3fe;
    border: none;
    border-radius: 4px;
  }
}

.menu-list {
  display: none;
  @media(min-width: 1024px) {
    display: flex;
    margin: 0;
    padding: 0;

    list-style: none;
  }

  @media(min-width: 1440px) {
    display: block;
  }
}

.menu-list__item {
  display: flex;
  align-items: center;
  padding: 10px;

  @media(min-width: 1440px) {
    height: 60px;
    padding: 10px 20px 10px 30px;
  }

  a {
    display: flex;

    font-weight: 300;
    font-size: 16px;
    line-height: 20px;
    color: var(--darkBlue);
    text-decoration: none;

    @media(min-width: 1024px) and (max-width: 1439px) {
      font-size: 14px;
    }

    svg {
      margin-right: 13px;

      color: #b4bef0;
    }
  }

  &--active,
  &:hover {
    position: relative;

    background: #f1f3fe;

    @media(min-width: 1440px) {
      &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;

        width: 6px;
        height: 60px;

        background: var(--accentColor);
        border-radius: 0 6px 6px 0;
      }
    }

    a {
      font-weight: 500;
      color: var(--accentColor);

      svg {
        color: var(--accentColor);
      }
    }
  }
}

.settings {
  display: none;

  &:hover,
  &:active,
  &:focus {
    text-decoration: underline;
  }

  @media(min-width: 1024px) {
    display: grid;
    width: 20px;
    height: 20px;
    overflow: hidden;

    text-decoration: none;
  }

  @media(min-width: 1440px) {
    align-items: center;
    width: auto;
    height: 82px;
    padding-left: 31px;

    column-gap: 10px;

    grid-template-columns: 20px auto;
  }
}
