// Layout styles

.page-grid {
  display: grid;
  width: 100%;
  min-height: 100vh;
  margin: 0 auto;

  grid-template-columns: 1fr;
  grid-template-rows: 80px auto 1fr auto;

  @media(max-width: 1023px) {
    gap: 0;
  }

  @media(min-width: 1440px) {
    max-width: 1440px;

    grid-template-columns: 280px 1fr;
    grid-template-rows: 80px 1fr 60px;
  }
}

