// Footer styles
.page-footer {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 30px;

  color: rgba(255,255,255,0.5);

  background: var(--darkBlue);

  @media(min-width: 1024px) {
    display: grid;

    grid-column: 1/-1;
    grid-template-columns: auto 1fr auto;
  }
  @media(min-width: 1440px) {
    padding: 0 30px;

    grid-template-columns: 280px 1fr 280px;
  }
}

.page-footer__copyright {
  margin-right: 20px;

  @media(min-width: 1024px) {
    margin-right: 0;
  }
}

.page-footer__title {
  text-align: center;
  white-space: nowrap;

  b {
    font-weight: 500;
  }
}

.page-footer__author {
  text-align: center;

  grid-column: 1 / -1;

  @media(min-width: 768px) {
    text-align: right;

    grid-column: 3 / 4;
  }
}
