// Typographic styles

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;

  font-family: var(--ff);
}

p {
  font-weight: 300;
  font-size: 14px;
  line-height: 143%;
  font-family: var(--ff);
}

li {
  font-size: 14px;
  font-family: var(--ff);
}

blockquote {
  padding: 20px 0 20px 30px;

  font-size: 110%;

  border-left: 3px solid #cccccc;
  cite {
    display: block;
    margin-top: 20px;

    font-size: 75%;

    &::before {
      content: "- ";
    }
  }
}
