// Base styles


*,
*::before,
*::after {
  box-sizing: border-box;
}

*:not(input):focus {
  outline: none;
}
.visuallyhidden {
  position: absolute;

  width: 1px;
  height: 1px;
  padding: 0;
  overflow: hidden;

  border: 0;

  clip: rect(1px, 1px, 1px, 1px);
}

body {
  width: 100%;
  margin: 0;
  padding: 0;

  font-weight: 300;
  font-family: var(--ff);
  color: var(--darkBlue);

  background-color: #ffffff;
}
