.main {
  padding: 30px;

  background-color: #f1f3fe;

  @media(min-width: 1024px) {
    display: grid;

    gap: 20px;
    grid-column: 1/-1;
    grid-template-areas: "slider contacts"
    "promo contacts"
    "video contacts";
    grid-template-columns: 3fr 1fr;
    grid-template-rows: 300px auto auto;
  }
  @media(min-width: 1440px) {
    grid-column: 2/-1;
    grid-row: 2/-2;
  }
}
