

.main__video {
  @media(min-width: 1024px) {
    display: grid;

    gap: 0 20px;
    grid-area: video;
    grid-template-columns: repeat(3, 1fr);
  }
}

.video__player {
  position: relative;

  overflow: hidden;

  border-radius: 4px;
  box-shadow: 0 1px 4px rgba(60, 66, 95, 0.2);

  @media(min-width: 1024px) {
    grid-column: 1/3;
  }

  img {
    width: 100%;
    height: 100%;

    object-fit: cover;
    object-position: center;
  }

  button {
    position: absolute;
    top: calc(50% - 32px);
    left: calc(50% - 32px);

    display: flex;
    justify-content: center;
    align-items: center;
    width: 64px;
    height: 64px;

    color: #ffffff;

    background: var(--accentColor);
    border: none;
    border-radius: 4px;
    cursor: pointer;

    &:hover,
    &:active,
    &:focus {
      opacity: 0.8;
    }
  }
}

.video__list {
  height: 300px;
  overflow: hidden;

  background: #ffffff;
  border-radius: 4px;
  box-shadow: 0 1px 4px rgba(60, 66, 95, 0.2);

  h3 {
    margin-left: 30px;
    padding: 18px 30px 10px 0;

    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    color: var(--darkBlue);

    border-bottom: 1px solid #e4e8fc;
  }
}

.videos {
  margin: 0;
  padding: 13px 0 0;
}

.videos__item {
  display: grid;
  align-items: center;
  min-height: 48px;
  padding: 7px 10px 7px 30px;

  gap: 0 12px;
  grid-template-columns: 32px 1fr;
  grid-template-rows: 18px auto;

  button {
    align-self: flex-start;
    width: 32px;
    height: 32px;
    padding: 0;

    color: #ffffff;

    background: #f1f3fe;
    border: none;
    border-radius: 4px;
    cursor: pointer;

    grid-row: 1/3;

    &:hover,
    &:active,
    &:focus {
      opacity: 0.8;
    }

    svg {
      fill: currentColor;
    }
  }

  h4 {
    opacity: 0.3;
  }

  p {
    line-height: 1.2;

    opacity: 0.3;
  }

  &--viewed {
    h4,
    p {
      opacity: 1;
    }

    button {
      color: var(--accentColor);
    }
  }
  &:hover,
  &--active {
    position: relative;

    background: #f1f3fe;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;

      width: 6px;
      height: 100%;

      background: var(--accentColor);
      border-radius: 0 6px 6px 0;
    }

    h4,
    p {
      opacity: 1;
    }

    button {
      color: #ffffff;

      background: var(--accentColor);
    }
  }
}
