
.main__contacts {
  display: none;

  background: #ffffff;
  border-radius: 4px;
  box-shadow: 0 1px 4px rgba(60, 66, 95, 0.2);

  grid-area: contacts;

  @media (min-width: 1024px) {
    display: block;
  }

  h3 {
    margin-left: 30px;
    padding: 20px 30px 10px 0;

    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    color: var(--darkBlue);

    border-bottom: 1px solid #e4e8fc;
  }
}

.contacts-list {
  padding: 21px 0;
}

.contacts-list__item {
  margin-bottom: 16px;
  padding: 0 20px 0 30px;
}

.contact {
  display: grid;

  text-decoration: none;

  grid-gap: 0 12px;
  grid-template-columns: 40px auto;
  grid-template-rows: auto auto;
}

.contact__avatar {
  position: relative;

  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;

  font-style: normal;
  font-weight: 900;
  font-size: 22px;
  line-height: 26px;
  text-align: center;
  color: #b4bef0;

  background: #f1f3fe;
  border-radius: 50%;

  grid-row: 1 / -1;

  &::after {
    position: absolute;
    top: 0;
    left: 0;

    width: 12px;
    height: 12px;

    background: #0bc4cf;
    border: 2px solid #ffffff;
    border-radius: 50%;
  }
}

.contact__name,
.contact__group {
  font-weight: 300;
  font-size: 14px;
  line-height: 20px;
  color: var(--darkBlue);
}

.contact__group {
  opacity: 0.5;
}

.contact--online .contact__avatar::after {
  content: "";
}

.contact:hover,
.contact:active {
  .contact__name {
    color: var(--accentColor);
  }
}
