
.main__promo {
  display: grid;
  margin-bottom: 20px;

  gap: 20px 0;
  grid-area: promo;
  grid-template-columns: 1fr;

  @media(min-width: 1024px) {
    margin-bottom: 0;

    gap: 0 20px;
    grid-template-columns: repeat(3, 1fr);
  }
}

.promo {
  display: grid;
  padding: 20px 30px;

  background: #ffffff;
  border-radius: 4px;
  box-shadow: 0 1px 4px rgba(60, 66, 95, 0.2);

  grid-template-rows: auto 1fr auto;
}

.promo__title {
  margin-bottom: 5px;

  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  color: var(--darkBlue);
}

.promo__desc {
  margin-bottom: 4px;
}

.promo__date {
  font-weight: 900;
  color: #0bc4cf;
}

.promo__action {
  margin-top: 7px;

  text-align: right;

  a {
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    color: var(--accentColor);
    text-decoration: none;

    &:hover,
    &:active,
    &:focus {
      color: var(--darkBlue);
    }
  }
}
