
.main__slider {
  position: relative;

  margin-bottom: 20px;
  overflow: hidden;

  background: var(--accentColor);
  border-radius: 4px;
  box-shadow: 0 1px 4px rgba(60, 66, 95, 0.2);

  grid-area: slider;

  @media(min-width: 1024px) {
    margin-bottom: 0;
  }
}

.slide-buttons {
  position: absolute;
  top: 0;
  right: -32px;
  left: -32px;
  z-index: 10;

  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;

  pointer-events: none;
}

.slide-buttons__item {
  width: 64px;
  height: 64px;

  background-color: #ffffff;
  background-repeat: no-repeat;
  background-size: 12px 20px;
  border: none;
  border-radius: 50%;
  cursor: pointer;

  pointer-events: all;

  &:hover {
    background-color: #d4e1f8;
  }

  &--prev {
    background-image: url("data:image/svg+xml,%3Csvg width='12' height='20' viewBox='0 0 12 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M11 1L2 10L11 19' stroke='%23B4BEF0' fill='none' stroke-width='2'/%3E%3C/svg%3E%0A");
    background-position: 75% 50%;
  }
  &--next {
    background-image: url("data:image/svg+xml,%3Csvg width='12' height='20' viewBox='0 0 12 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 1L10 10L1 19' stroke='%23B4BEF0' stroke-width='2'/%3E%3C/svg%3E%0A");
    background-position: 25% 50%;
  }
}

.slide {
  position: relative;

  display: flex;
  align-items: center;
  height: 300px;

  &::after {
    content: "";
    position: absolute;
    z-index: 1;

    width: 100%;
    height: 100%;

    background-color: rgba(60, 66, 95, 0.70);
  }

  @media(min-width: 1024px) {
    height: 100%;

    &::after {
      display: none;
    }
  }

  img {
    position: absolute;

    width: 100%;
    height: 100%;

    object-fit: cover;
    object-position: right;
  }
}

.slide__text {
  position: relative;
  z-index: 2;

  padding-right: 40px;
  padding-left: 40px;

  color: #ffffff;

  @media(min-width: 1024px) {
    max-width: 66%;
  }

  h2 {
    margin-bottom: 10px;

    font-weight: 900;
    font-size: 22px;
    line-height: 1.7;

    @media(min-width: 1024px) {
      font-size: 28px;
    }
  }

  p {
    margin-bottom: 24px;

    font-weight: 300;
    font-size: 14px;
    line-height: 1.55;

    @media(min-width: 1024px) {
      font-size: 18px;
    }
  }

  .slide__action {
    padding: 10px 20px;

    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    color: #ffffff;
    text-decoration: none;

    background: #ff8f0b;
    border-radius: 4px;

    transition: background 0.35s ease;

    &:hover,
    &:focus,
    &:active {
      background: #e98513;
    }
  }
}
